import { Fragment, lazy, useContext } from "react";
import { Navigate, Route, Routes } from "react-router-dom";
import { PublicPaths } from "./path";
import { AuthContext } from "../context";

const privateRoutes = [
  /* Add paths for authorized users */
  {
    path: "clockin",
    element: lazy(() => import("../modules/QRCode/Clockin")),
  },
  {
    path: "clockout",
    element: lazy(() => import("../modules/QRCode/Clockout")),
  },
  {
    path: "home",
    element: lazy(() => import("../modules/QRCode/Clockin/home")),
  },
  {
    path: "admin/dashboard",
    element: lazy(() => import("../modules/Admin/Dashboard/index")),
  },

  {
    path: "admin/report/:key",
    element: lazy(() => import("../modules/Admin/Report/[key]")),
  },
  {
    path: "admin/users",
    element: lazy(() => import("../modules/Admin/Users/index")),
  },
  {
    path: "admin/user/:id",
    element: lazy(() => import("../modules/Admin/Users/userHistory")),
  },
  {
    path: "admin/settings",
    element: lazy(() => import("../modules/Admin/Settings/index")),
  },
  {
    path: "admin/settings/location",
    element: lazy(() => import("../modules/Admin/Settings/location")),
  },
  {
    path: "admin/settings/auth",
    element: lazy(() => import("../modules/Admin/Settings/authentication")),
  },
];

function User() {
  const { user } = useContext(AuthContext);
  if (!user) {
    return <Navigate to={`${PublicPaths.HOME}`} replace />;
  }
  return (
    <Routes>
      {privateRoutes.map(({ path, element: Element }) => (
        <Fragment key={path}>
          <Route key={path} path={path} element={<Element />} />
        </Fragment>
      ))}
    </Routes>
  );
}

export default User;
